.pin_title {
    width: 0;
    height: 0;
    opacity: 0;
}

.pin_modal {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    opacity: 0;
    background-color: rgba(0, 0, 0, 0.1);
    transition-duration: 0.3s;
}

.pin_modal:hover {
    opacity: 1;
}

.pin_modal .modal_head {
    width: 100%;
    height: 15%;
    position: fixed;
    top: 0;

    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.pin_modal .modal_foot {
    width: 100%;
    height: 15%;
    position: fixed;
    bottom: 0;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.pint_mock_icon_container {
    width: 32px;
    height: 32px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    background-color: #F0F0F0;
    cursor: pointer;
}

.pint_mock_icon {
    width: 50%;
}

.pin_modal .modal_head .save_card {
    margin-right: 20px;
    width: 53px;
    height: 40px;
    border-radius: 22px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 14px;
    font-weight: 700;

    color: white;
    background-color: #E60023;
    cursor: pointer;
}

.pin_modal .modal_foot .destination {
    width: 132px;
    height: 32px;
    border-radius: 18px;

    display: flex;
    align-items: center;

    font-size: 16px;
    font-weight: 700;

    background-color: #F0F0F0;
    cursor: pointer;
}

.pin_modal .modal_foot .destination span {
    overflow: hidden;
    white-space: nowrap;
}

.pin_image {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.pin_image img {
    opacity: 0;
}

.pin_max_width {
    max-width: 100%;
}

.pin_max_height {
    max-height: 100%;
}