.add_pin_modal {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999;

  background-color: rgba(0, 0, 0, 0.5);
}

.add_pin_container {
  padding: 35px;
  width: 880px;
  height: 674px;
  border-radius: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;

  display: flex;

  background-color: white;
}

.side {
  padding: 20px;
  width: 50%;
  height: 100%;
}

#left_side {
  /* background-color: blue; */
}

#right_side {
  /* background-color: orange; */
}

.pint_mock_icon_container {
  width: 32px;
  height: 32px;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;

  background-color: #f0f0f0;
  cursor: pointer;
}

.pint_mock_icon {
  width: 50%;
}

.section1 {
  width: 100%;
  height: 8%;

  /* background-color: purple; */
}

.section2 {
  padding: 0px 10px;
  width: 100%;
  height: 80%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* background-color: aquamarine; */
}

.section3 {
  width: 100%;
  height: 12%;

  display: flex;
  justify-content: center;
  align-items: center;

  /* background-color: firebrick; */
}

#left_side .section1 .pint_mock_icon_container {
  width: 40px;
  height: 40px;

  background-color: rgba(239, 239, 239, 0);
  transition-duration: 0.3s;
}

#left_side .section1 .pint_mock_icon_container:hover {
  background-color: rgba(239, 239, 239, 1);
}

#left_side .section2 #upload_img_label {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

#left_side .section2 #upload_img_label input {
  width: 0;
  height: 0;
  opacity: 0;
}

#left_side .section2 #upload_img_label .upload_img_container {
  padding: 10px;
  width: 100%;
  height: 100%;
  border-radius: 8px;

  background-color: #efefef;
}

#left_side .section2 #upload_img_label .upload_img_container #dotted_border {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  border: 2px dashed #dadada;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#left_side .section2 .pint_mock_icon_container {
  background-color: #767676;
}

.modals_pin {
  width: 252px;
  height: 512px;
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  display: none;
}

.pin_image {
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

